$fonts-dir: '/fonts' !default;
$default-font-family: 'OpenSans';
$default-font-path: '/OpenSans';

@mixin font-face($variant, $ff: $default-font-family, $fp: $default-font-path) {
  font-family: $ff;
  src: url('#{$fonts-dir}#{$fp}-#{$variant}.woff2') format('woff2'),
    url('#{$fonts-dir}#{$fp}-#{$variant}.woff') format('woff'),
    url('#{$fonts-dir}#{$fp}-#{$variant}.ttf') format('truetype');
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}

@font-face {
  @include font-face('Light');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  @include font-face('Light');
  font-weight: 300;
  font-style: italic;
}

@font-face {
  @include font-face('Regular');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  @include font-face('SemiBold');
  font-weight: 600;
  font-style: normal;
}

@font-face {
  @include font-face('Bold');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  @include font-face('NarrowLight', 'OpenSansNarrow');
  font-weight: 300;
  font-style: normal;
}
