  border: none;
  border-radius: none;
  min-height: 30px;
}
:not(.dt-form-field-infix) > .dt-input:focus:not([disabled]), :not(.dt-inline-editor-infix) > .dt-input:focus:not([disabled]) {
  outline-width: 2px;
  outline-style: solid;
  outline-offset: 1px;
  outline-color: #b7b7b7;
}
.dt-input[disabled] {
  background-color: #f2f2f2;
  color: #cccccc;
}
